<template>
  <v-container id="login" fill-height tag="section">
    <v-row justify="center">
      <v-slide-y-transition appear>
        <base-material-card color="primary " max-width="100%" width="350px" class="px-5 py-3">
          <template v-slot:heading>
            <div class="text-center">
              <!-- <v-icon v-text="social.icon" /> -->
              <img src="../../assets/logoSoft.png" width="100px" />
              <p class="mb-2">Softband Team</p>
            </div>
          </template>
          <form v-on:submit.prevent="login">
            <v-card-text class="text-center">
              <div class="text-center grey--text body-1 font-weight-light">نظام المذاخر</div>

              <v-text-field
                style="direction: ltr"
                outlined
                color="primary darken-1 "
                label="رقم المستخدم"
                class="mt-10"
                v-model="username"
                :rules="[rules.required, rules.minPhon]"
                v-mask="mask"
                placeholder="07XX XXX XXXX"
                onkeypress="return (event.charCode >= 48 && event.charCode <= 57)"
              />

              <v-text-field
                outlined
                class="mb-8"
                color="primary darken-1"
                label="كلمة المرور"
                v-model="password"
                :rules="[rules.required, rules.min]"
                :type="show1 ? 'text' : 'password'"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="show1 = !show1"
              />
            </v-card-text>
            <v-col class="text-center">
              <v-btn
                :loading="loading"
                @click.prevent="login()"
                type="submit"
                large
                text
                color="primary darken-1"
                ref="sendReply"
              >تسجيل الدخول</v-btn>
            </v-col>
          </form>
        </base-material-card>
      </v-slide-y-transition>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
import { mask } from "vue-the-mask";

export default {
  directives: {
    mask
  },
  data: () => ({
    mask: "07XX XXX XXXX",
    show1: false,
    rules: {
      required: value => !!value || "مطلوب",
      min: v => v.length >= 6 || "كلمة المرور يجب ان تتكون من 6 عناصر او اكثر",
      minPhon: v => v.length == 13 || "رقم الهاتف يجب ان يتكون من 11 رقم"
    },
    username: "",
    password: "",
    loading: false
  }),

  methods: {
    login() {
      if (this.username && this.password) {
        this.loading = true;
        const data = {
          phoneNumber: this.username.replace(/ /g, ""),
          password: this.password
        };
        axios
          .post("auth/login", data)
          .then(res => {
            this.loading = false;
            // console.log(res);
            this.$store.dispatch("login", res.data);
            this.$router.push("/admin/dashboard");
          })
          .catch(err => {
            this.loading = false;

            Swal.fire({
              title: "رقم المستخدم او كلمة المرور غير صحيح",
              text: "",
              icon: "error",
              confirmButtonText: "اغلاق"
            });

            // console.log(err);
          });
      } else {
        Swal.fire({
          title: "تاكد من ادخال رقم المستخدم وكلمة المرور",
          text: "",
          icon: "error",
          confirmButtonText: "اغلاق"
        });
      }
    }
  },
  created() {
    this.$store.dispatch("logout");
  }
};
</script>
